.page-posts-single {
  .posts-sidebar {
    @include whitebox();
    padding: 40px;

    h4 {
      text-transform: capitalize;
    }

    ul {
      margin-bottom: 10px;

      li {
        text-transform: capitalize;
      }
    }
  }

  .post-author {
    display: flex;
    margin-bottom: 30px;

    .post-author-avatar {
      margin-right: 10px;
      width: 60px;
      height: 60px;
      overflow: hidden;
      border-radius: 3px;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center top;
      }
    }

    .post-author-info {
      .post-author-name {
        display: block;
        font-size: 1.3rem;
        color: #222;
      }

      span.post-author-date {
        display: block;
        color: #666;
        font-size: 1.1rem;
      }
    }
  }
}