body {
    font-size: 1.1rem;

    @include media-breakpoint-up(sm) {
        font-size: 1rem;
    }
}

svg {
    display: block;

    polygon {
        display: inline-block;
    }
}

.spacer {
    margin-top: 30px;
}

ul.terms {
    padding: 30px 0;

}

@include media-breakpoint-down(sm) {
    $grid-gutter-width: $grid-gutter-width-sm;

    .container {
        padding-left: calc($grid-gutter-width / 2);
        padding-right: calc($grid-gutter-width / 2);
    }
}