.team-summary {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  text-align: center;
  @include whitebox();
  padding: 30px;
  height: 100%;
  @include media-breakpoint-up(md) {
    text-align: left;
  }
  .team-image.director {
    @include media-breakpoint-up(lg) {
      width: 240px;
      height: 306px;
    }
    img {
      width: 240px;
      height: 306px;
      border-radius: 3px;
      display: inline-block;
      object-fit: cover;
      object-position: center top;
      @include media-breakpoint-up(md) {
        width: 240px;
        height: 306px;
      }
    }
    // Old aspect ratio (2:3) for comparison
    &.old-ratio {
      @include media-breakpoint-up(lg) {
        height: 360px;
      }
      img {
        height: 360px;
        @include media-breakpoint-up(md) {
          height: 360px;
        }
      }
    }
  }
  .team-image {
    
    text-align: center;
    width: 100%;
    @include media-breakpoint-up(md) {
      text-align: left;
      margin-right: 20px;
    }
    @include media-breakpoint-up(lg) {
      width: 150px;
      margin-right: 20px;
    }
    img {
      width: 200px;
      height: 255px;
      border-radius: 3px;
      display: inline-block;
      object-fit: cover;
      object-position: center top;
      @include media-breakpoint-up(md) {
        width: 150px;
        height: 191px;
      }
    }
    // Old aspect ratio (2:3) for comparison
    &.old-ratio {
      img {
        height: 300px;
        @include media-breakpoint-up(md) {
          height: 225px;
        }
      }
    }
  }
  .team-block {
    @include media-breakpoint-up(md) {
      flex: 1;
    }
    .team-meta {
      h2 {
        margin: 0;
      }
      p {
        margin: 0;
      }
      .team-name {
      }
      .team-jobtitle {
        font-size: 1.2rem;
        color: #333;
      }
      .team-links {
        margin-top: 6px;
        i {
          font-size: 1.8rem;
          margin-right: 3px;
        }
        a {
          &:last-of-type {
            i {
              margin-right: 0;
            }
          }
        }
      }
    }
    .team-content {
      margin-top: 20px;
      @include media-breakpoint-up(lg) {
        width: auto;
      }
    }
  }
}
