.page-posts-list {
  .posts-sidebar {
    @include whitebox();
    padding: 40px;

    h4 {
      text-transform: capitalize;
    }

    ul {
      margin-bottom: 10px;

      li {
        text-transform: capitalize;
      }
    }
  }

  .post-summary {
    .post-meta {
      .post-author {
        .post-author-avatar {
          width: 40px;
          height: 40px;
          overflow: hidden;
          border-radius: 3px;

          img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center top;
          }
        }
      }
    }
  }
}